// src/pages/admin/index.js
import { useState } from "react";
import { motion } from "framer-motion";
import Link from "next/link";
import { Sun, Moon, Users, Briefcase, FileText } from "lucide-react";

export default function AdminDashboard() {
  const [darkMode, setDarkMode] = useState(false);

  return (
    <div className={`${darkMode ? "dark bg-gray-900 text-white" : "bg-gray-50 text-gray-900"} min-h-screen flex`}>
      {/* Sidebar */}
      <aside className="w-64 bg-orange-500 text-white p-6 flex flex-col justify-between">
        <div>
          <h1 className="text-2xl font-bold mb-8">Khedme Admin</h1>
          <nav className="flex flex-col gap-4">
            <Link href="/admin/providers" className="hover:bg-orange-600 rounded-lg p-2 flex items-center gap-2">
              <Users size={18}/> Providers
            </Link>
            <Link href="/admin/jobs" className="hover:bg-orange-600 rounded-lg p-2 flex items-center gap-2">
              <Briefcase size={18}/> Jobs
            </Link>
            <Link href="/admin/applications" className="hover:bg-orange-600 rounded-lg p-2 flex items-center gap-2">
              <FileText size={18}/> Applications
            </Link>
            {/* ðŸ†• New Settings Page */}
            <Link href="/admin/settings" className="hover:bg-orange-600 rounded-lg p-2 flex items-center gap-2">
            <FileText size={18}/> Settings
            </Link>
         </nav>
        </div>

        <button
          onClick={() => setDarkMode(!darkMode)}
          className="flex items-center gap-2 border-t border-orange-400 pt-4 mt-6"
        >
          {darkMode ? <Sun size={18}/> : <Moon size={18}/>}
          {darkMode ? "Light Mode" : "Dark Mode"}
        </button>
      </aside>

      {/* Main Content */}
      <motion.main
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 0.4 }}
        className="flex-1 p-10"
      >
        <h2 className="text-3xl font-bold mb-4">Welcome to the Admin Dashboard ðŸ‘‹</h2>
        <p className="text-gray-500 dark:text-gray-400">
          Use the sidebar to manage providers, jobs, and applications.
        </p>
      </motion.main>
    </div>
  );
}
